.main-menu {
  padding-left: 0;
  margin: 0;
  display: flex;
  position: relative;

  &__dropdown {
    position: absolute;
    top: calc(100% + 1px);
    left: 0;
    width: 100% ;
  }

  &__item {
    list-style-type: none;
    padding-left: 0;
    }

  &__item.active #{&}__link,
  &__item:hover #{&}__link {
    color: #3e52e7;
  }

  &__item:hover:before {
    opacity: 1;
  }

  &__link {
    display: block;
    color: #000000;
    font-size: 16px;
    transition: color .3s ease;
    white-space: nowrap;
    padding: 18px 20px 30px;
    font-weight: 700;
    position: relative;


    &:before {
      content: '';
      position: absolute;
      top: 24px;
      right: 0;
      width: 1px;
      height: 10px;
      background-color: #000000;
      transition: .3s ease;
      z-index: 10;
      transform: rotate(20deg)
    }


    &:after {
      content: '';
      opacity: 0;
      position: absolute;
      bottom: -3px;
      left: 0;
      width: 100%;
      height: 6px;
      border-radius: 3px;
      background-color: #005eff;
      transition: .3s ease;
      z-index: 4;
    }

  }
  &__item:last-child #{&}__link:before {
    display: none;
  }
  &__item.active #{&}__link:after {
    opacity: 1;
  }
  &__item.hide-active #{&}__link:after {
    opacity: 0;
  }



  &__item:hover #{&}__link:after {
    opacity: 1;
  }



}
